import React, { useEffect, useState } from "react";
import Back from "./common/Back";
import { useSelector, useDispatch } from "react-redux";
import {
  addToCarts,
  removeFromCart,
  clearItem,
  clearCart,
} from "../redux/CartSlice";
import {
  addToCarts1,
  removeFromCart1,
  clearItem1,
  clearCart1,
} from "../redux/CartSlice1";
import { Link, useNavigate } from "react-router-dom";
import { toast, ToastContainer } from "react-toastify";
import ScrollToTop from "./scrolltotop";
import Cookies from "js-cookie";
import CryptoJS from "crypto-js";
import axios from "axios";
import { message } from "antd";

export default function Cart() {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;

  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    : null;

  const items = useSelector((state) => state.cart.items);
  const items1 = useSelector((state) => state.cart1?.items1);
  const subtotal = useSelector((state) => state.cart.subtotal);
  const total = useSelector((state) => state.cart.total);
  const subtotal1 = useSelector((state) => state.cart1.subtotal1);
  const total1 = useSelector((state) => state.cart1.total1);

  const textEncy = Cookies.get("AllText");
  const AllText = textEncy
    ? JSON.parse(
        CryptoJS.AES.decrypt(textEncy, encryptionKey).toString(
          CryptoJS.enc.Utf8
        )
      )
    : null;

  const [categoryData, setCategoryData] = useState([]);

  const getCategories = async () => {
    try {
      const res = await axios.get(
        "https://majesticmall.net/api/admin/getCategory",
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        setCategoryData(
          res.data?.category.filter((item) => item.status === "Active")
        );
      }
    } catch (error) {
      console.error("Error fetching categories:", error);
    }
  };

  useEffect(() => {
    getCategories();
  }, []);

  const [deliveryCharges, setDeliveryCharges] = useState({});
  const fetchDeliveryCharges = async () => {
    try {
      const res = await axios.get(
        "https://majesticmall.net/api/admin/getactivedelivery",
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        setDeliveryCharges(res.data?.data);
      }
    } catch (error) {
      console.error("Error fetching delivery charges:", error);
      // message.error("Failed to fetch delivery charges");
    }
  };

  useEffect(() => {
    fetchDeliveryCharges();
  }, []);

  const categoryid = categoryData?.find(
    (ele) => ele?.categoryName == "Fruits & Vegetables"
  );

  const encryptedUser = Cookies.get("user");
  // const user = JSON.parse(CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(CryptoJS.enc.Utf8));
  // const encryptedUser = Cookies.get('user');
  const user = encryptedUser
    ? JSON.parse(
        CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(
          CryptoJS.enc.Utf8
        )
      )
    : null;

  const [activeTab, setActiveTab] = useState("normal");
  let vegDeliveryCh = 0;
  let showTxt = Cookies.get("AllTextHead");
  let cachedCategoryId = false;
  let minOrder = 0;
  let minKm = 0;
  let perKmCharge = 0;
  const checkDelvery = (item) => {
    // Check if the category is already cached
    if (item.category === categoryid?._id) {
      if (!cachedCategoryId) {
        cachedCategoryId = categoryid?._id;
        vegDeliveryCh = AllText?.deliveryCharge || 0;

        minOrder = AllText?.minOrder || 0;
        minKm = AllText?.minKm || 0;
        perKmCharge = AllText?.perKmCharge || 0;
        cachedCategoryId = true;
        // console.log("vegDeliveryCh", cachedVegDeliveryCh);
      }
    } else {
      return false;
    }
  };

  const emptyCartMsg = (
    <h4 className="container py-4 mx-auto text-2xl font-semibold text-center text-gray-700">
      Your Cart is Empty
    </h4>
  );

  const handleCheckout = (deliveryCharge, minKm, perkmprice, minAmount) => {
    if (!user) {
      toast.info("Please login as a user to proceed with checkout.");
      setTimeout(() => {
        navigate("/login");
      }, 2000);
      return;
    } else {
      navigate("/checkout", {
        state: {
          orderType: activeTab,
          deliveryCharge: deliveryCharge,
          minKm,
          perkmprice,
          minAmount,
        },
      });
    }
  };

  const scrollToTop = (val) => {
    navigate(`/shop-detail/${val?._id}`);
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  const scrollToTop1 = (val) => {
    navigate(`/shop-detail1/${val?._id}`);
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  const renderCartItems = (items) => {
    return (
      <ul className="list-group list-group-flush">
        {items.map((item) => {
          checkDelvery(item);
          return (
            <li
              key={item._id}
              className="px-0 py-3 list-group-item py-lg-0 border-top"
            >
              <div className="flex flex-wrap items-start gap-3 lg:gap-0">
                <div className="w-1/4 md:w-1/6">
                  <div
                    style={{ cursor: "pointer" }}
                    onClick={() =>
                      activeTab === "normal"
                        ? scrollToTop(item)
                        : scrollToTop1(item)
                    }
                  >
                    <img
                      src={`https://majesticmall.net/product/${item?.Photos[0]?.file}`}
                      alt="Ecommerce"
                      loading="lazy"
                      className="object-contain w-24 h-24 md:w-28 md:h-28"
                      onError={(e) => {
                        e.target.src = "../img/example.jpg"; // Replace with your default image URL
                      }}
                    />
                  </div>
                </div>
                <div className="w-1/4 mt-2 md:w-1/3">
                  <h6 className="mb-1 text-lg md:text-xl">
                    {item.productname}
                  </h6>
                  <small className="text-muted">
                    {item?.selectedWeight?.value}
                    {item?.selectedWeight?.units}
                  </small>
                </div>
                {item.stock - item?.sellproduct <= 0 ? (
                  <div className="flex items-center justify-center w-1/4 md:w-1/8">
                    <p
                      style={{
                        color: "red",
                        paddingTop: "27px",
                        textAlign: "center",
                        fontWeight: "500",
                      }}
                    >
                      Out of Stock
                    </p>
                  </div>
                ) : (
                  <div className="flex items-center justify-center w-1/4 md:w-1/8">
                    <button
                      onClick={() => {
                        if (item.quantity === 1) {
                          try {
                            toast.warn("Quantity cannot be less than 1.");
                          } catch (error) {
                            console.log(error);
                          }
                        } else {
                          if (activeTab === "normal") {
                            dispatch(removeFromCart(item));
                          } else {
                            dispatch(removeFromCart1(item));
                          }
                        }
                      }}
                      className="px-2 py-2 mx-1 bg-gray-100 rounded-full"
                    >
                      <i className="fa fa-minus"></i>
                    </button>

                    <p className="mx-2 mt-3 text-center">{item.quantity}</p>
                    <button
                      onClick={() => {
                        if (activeTab === "normal") {
                          dispatch(addToCarts(item));
                        } else {
                          dispatch(addToCarts1(item));
                        }
                      }}
                      className="px-2 py-2 mx-1 bg-gray-100 rounded-full"
                    >
                      <i className="fa fa-plus"></i>
                    </button>
                  </div>
                )}

                <div className="w-full text-center md:mt-0 md:text-right">
                  <span className="text-lg font-bold md:text-xl">
                    ₹
                    {parseFloat(
                      (item?.selectedWeight?.price -
                        item?.selectedWeight?.price *
                          ((item.selectedWeight?.discount
                            ? item.selectedWeight?.discount
                            : item?.discount) /
                            100)) *
                        item.quantity
                    )?.toFixed(2)}
                  </span>
                  <span
                    className="ml-16 cursor-pointer text-danger"
                    onClick={() => {
                      if (activeTab === "normal") {
                        dispatch(clearItem(item));
                      } else {
                        dispatch(clearItem1(item));
                      }
                    }}
                  >
                    <i className="fa fa-trash text-l"></i>
                  </span>
                </div>
              </div>
            </li>
          );
        })}
      </ul>
    );
  };

  useEffect(() => {
    if (items1.length > 0) {
      setActiveTab("normal");
    }
  }, []);
  // console.log("deliveryCharges",deliveryCharges);
  const [totalDiscount, settotalDiscount] = useState(0);
  useEffect(() => {
    const totalDiscount = (activeTab !== "scheduled" ? items : items1).reduce(
      (total, item) => {
        // Calculate the discount for the current item
        const discount =
          (item.selectedWeight?.price * item?.quantity || 0) *
          ((item.selectedWeight?.discount ?? item?.discount ?? 0) / 100);

        return total + discount; // Accumulate the total discount
      },
      0 // Initial total discount is 0
    );
    settotalDiscount(totalDiscount);
  }, [items, items1, activeTab]);

  return (
    <>
      {/* <Back title="Cart" /> */}
      <ScrollToTop />
      <ToastContainer />
      <section className="mt-8 mb-8">
        <div className="container px-4 py-8 mx-auto bg-gray-50">
          <div className="text-center">
            {/* <h1 className="text-3xl font-bold text-gray-800 md:text-4xl">
              Shop Cart
            </h1> */}
            <h1
              className="text-2xl md:text-4xl lg:text-4xl font-bold text-center quicksand mt-1 text-[#000]"
              // style={{ color: "#45595b" }} a3d04f
            >
              Shop Cart
            </h1>
            <div className="flex items-center justify-center p-0 m-0 mb-4">
              <span className="w-[10%] h-1 gradient1 rounded-md m-0 p-0"></span>
            </div>
          </div>

          <div className="mb-4">
            <ul className="items-center justify-center space-y-2 nav nav-tabs">
              <li className="nav-item">
                <button
                  className={`  nav-link mt-2 w-[150px] lg:w-[200px] ${
                    activeTab === "normal" ? "active" : ""
                  }`}
                  onClick={() => setActiveTab("normal")}
                >
                  Normal Orders
                </button>
              </li>
              <li className="nav-item">
                <button
                  className={` nav-link w-[150px] lg:w-[200px] ${
                    activeTab === "scheduled" ? "active" : ""
                  } `}
                  onClick={() => setActiveTab("scheduled")}
                >
                  Scheduled Orders
                </button>
              </li>
            </ul>
          </div>

          {items.length === 0 && items1.length === 0 ? (
            emptyCartMsg
          ) : activeTab == "normal" && items?.length === 0 ? (
            emptyCartMsg
          ) : activeTab !== "normal" && items1?.length === 0 ? (
            emptyCartMsg
          ) : (
            <div className="flex flex-col md:flex-row">
              <div className="md:w-2/3 ">
                {activeTab === "normal"
                  ? renderCartItems(items)
                  : renderCartItems(items1)}
                <div className="flex flex-row items-center">
                  <button
                    onClick={() => {
                      activeTab === "normal"
                        ? dispatch(clearCart())
                        : dispatch(clearCart1());
                    }}
                    className="bg-[#be343b] text-white font-semibold py-[12px] px-4 mt-4 rounded-full mr-2 hover:opacity-80 "
                  >
                    Clear Cart
                  </button>
                  <Link to="/">
                    <button className="bg-[#000] text-white font-semibold py-[12px] px-4 mt-4 rounded-full hover:opacity-80 ">
                      Continue Shopping
                    </button>
                  </Link>
                </div>
              </div>

              <div className="md:w-1/3">
                <div className="p-6 mt-6 card md:mt-0">
                  <h2 className="mb-4 h5">Summary</h2>
                  {activeTab === "normal" ? (
                    <div className="mb-2 card">
                      <ul className="list-group list-group-flush">
                        <li className="list-group-item d-flex justify-content-between">
                          <span>Total MRP</span>
                          <span className="text-end">
                            ₹{(subtotal + totalDiscount)?.toFixed(2)}
                          </span>
                        </li>
                        <li className="list-group-item d-flex justify-content-between">
                          <span>Discount</span>
                          <span className="text-end">
                            ₹{totalDiscount?.toFixed(2)}
                          </span>
                        </li>
                        <li className="list-group-item d-flex justify-content-between">
                          <span>Shipping Fee</span>
                          <span className="text-end">
                            ₹
                            {(total >= Number(deliveryCharges?.minAmount)
                              ? 0
                              : deliveryCharges?.deliveryCharges || 0
                            )?.toFixed(2)}
                          </span>
                        </li>
                        <li className="font-bold list-group-item d-flex justify-content-between">
                          <span>Total</span>
                          <span className="text-end">
                            ₹
                            {(
                              total +
                              (total >= Number(deliveryCharges?.minAmount)
                                ? 0
                                : deliveryCharges?.deliveryCharges || 0)
                            )?.toFixed(2)}
                          </span>
                        </li>
                      </ul>
                    </div>
                  ) : (
                    <div className="mb-2 card">
                      <ul className="list-group list-group-flush">
                        <li className="list-group-item d-flex justify-content-between">
                          <span>Total MRP</span>
                          <span className="text-end">
                            ₹{(subtotal1 + totalDiscount)?.toFixed(2)}
                          </span>
                        </li>
                        <li className="list-group-item d-flex justify-content-between">
                          <span>Discount</span>
                          <span className="text-end">
                            ₹{totalDiscount?.toFixed(2)}
                          </span>
                        </li>
                        <li className="list-group-item d-flex justify-content-between">
                          <span>Shipping Fee</span>
                          <span className="text-end">
                            ₹
                            {(total1 >= Number(minOrder)
                              ? 0
                              : vegDeliveryCh
                            )?.toFixed(2)}
                          </span>
                        </li>

                        <li className="font-bold list-group-item d-flex justify-content-between">
                          <span>Total</span>
                          <span className="text-end">
                            ₹
                            {(
                              total1 +
                              (vegDeliveryCh
                                ? vegDeliveryCh
                                : total1 >=
                                  Number(deliveryCharges?.minAmount || 0)
                                ? 0
                                : deliveryCharges?.deliveryCharges || 0)
                            )?.toFixed(2)}
                          </span>
                        </li>
                      </ul>
                    </div>
                  )}

                  {activeTab !== "normal" ? (
                    showTxt ? (
                      <p className="p-2 py-2 mt-2 text-sm text-center text-white rounded-md gradient1">
                        {showTxt}
                      </p>
                    ) : (
                      <></>
                    )
                  ) : (
                    <p className="p-2 py-2 mt-2 text-sm text-center text-white rounded-md gradient1">{`₹${
                      deliveryCharges?.deliveryCharges || 0
                    } Delivery charge for orders below  ₹${
                      deliveryCharges?.minAmount
                    }`}</p>
                  )}
                  <div className="flex items-center justify-center">
                    <button
                      className="bg-[#000] text-white font-semibold py-[12px] lg:w-[60%] px-4 rounded-full w-full hover:opacity-80 "
                      onClick={() => {
                        activeTab === "normal"
                          ? handleCheckout(
                              total >= Number(deliveryCharges?.minAmount)
                                ? 0
                                : deliveryCharges?.deliveryCharges || 0,
                              deliveryCharges?.minKm,
                              deliveryCharges?.perKmPrice || 0,
                              deliveryCharges?.minAmount
                            )
                          : !AllText?.category
                          ? showTxt
                            ? message.error(showTxt)
                            : handleCheckout(
                                total1 >= Number(minOrder) ? 0 : vegDeliveryCh,
                                minKm,
                                perKmCharge,
                                minOrder
                              )
                          : handleCheckout(
                              total1 >= Number(minOrder) ? 0 : vegDeliveryCh,
                              minKm,
                              perKmCharge,
                              minOrder
                            );
                      }}
                    >
                      Go to Checkout
                    </button>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </section>
    </>
  );
}
